@use "../abstracts" as *;

:root {
  @include light-theme;

  @media (prefers-color-scheme: light) {
    @include light-theme;
  }

  @media (prefers-color-scheme: dark) {
    @include dark-theme;
  }
}

.light-theme {
  @include light-theme;
}

.dark-theme {
  @include dark-theme;
}
